import { Component, OnInit } from '@angular/core';
import { AuthenticationService } from 'src/app/_services/authentication.service';
import { ModalController, NavController, AlertController, LoadingController } from '@ionic/angular';

import { first } from 'rxjs/operators';

import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-login',
  templateUrl: './login.page.html',
  styleUrls: ['./login.page.scss'],
  providers: [AuthenticationService]
})
export class LoginPage implements OnInit {

  films: Observable<any>;

  subsLogin: any;

  username: any;
  password: any;

  loading: any;
  loadingPresent: any;

  type = 'password';
  icon = 'eye-off-outline';
  iconColor = 'medium';

  constructor(
    private authService: AuthenticationService,
    public NavController: NavController,
    public httpClient: HttpClient,
    public loadingController: LoadingController,
    public alertController: AlertController
  ) {

  }

  ngOnInit() {
  }

  async presentLoading() {
    this.loadingPresent = true;
    this.loading = await this.loadingController.create({
      cssClass: 'my-custom-class',
      message: 'Loading...',
      mode: "ios",
      duration: 2000
    });
    await this.loading.present();
  }

  async presentAlertConfirm() {
    const alert = await this.alertController.create({
      cssClass: 'my-custom-class',
      header: 'Gagal Login',
      message: 'Username dan Password Anda Tidak Cocok',
      mode: "ios",
      backdropDismiss: false,
      buttons: [
        {
          text: 'Tutup',
          role: 'cancel',
          cssClass: 'secondary',
          handler: (blah) => {
            console.log('Confirm Cancel: blah');
          }
        }
      ]
    });

    await alert.present();
  }

  loginUser() {

    this.presentLoading();

    this.subsLogin = this.authService.loginUser(this.username, this.password)
      .pipe(first())
      .subscribe(
        data => {
          // console.log(data);          
          this.NavController.navigateRoot('/home', { replaceUrl: true });

        },
        error => {
          this.presentAlertConfirm();
          // console.log(error);
        });

  }

  showPassword() {

    if (this.type == 'password') {
      this.type = 'text';
      this.icon = 'eye-outline';
      this.iconColor = 'primary';
    } else {
      this.type = 'password';
      this.icon = 'eye-off-outline';
      this.iconColor = 'medium';
    }


  }

}
