import { Component, OnInit } from '@angular/core';

import { ActivatedRoute } from '@angular/router';
import { AuthenticationService } from 'src/app/_services/authentication.service';

import { LoadingController, NavController, AlertController, PickerController, ToastController } from '@ionic/angular';
import { ApiService } from 'src/app/_services/api.service';

import { first } from 'rxjs/operators';

@Component({
  selector: 'app-form4',
  templateUrl: './form4.page.html',
  styleUrls: ['./form4.page.scss'],
  providers: [ApiService, AuthenticationService]
})
export class Form4Page implements OnInit {

  params: any;
  subscribe: any;
  loading: any;
  subsLogin: any;

  username = '';

  fullname = '';
  phoneNumber = '';
  password = '';
  repassword = '';

  isKepalaDesa: any;
  isReadOnly: any;

  errorPassword = false;

  constructor(
    private NavController: NavController,
    private route: ActivatedRoute,
    public toastController: ToastController,
    public alertController: AlertController,
    private apiService: ApiService,
    private authService: AuthenticationService,
    public loadingController: LoadingController
  ) {

    this.params = this.route.snapshot.params;

    this.isKepalaDesa = this.params.isKepalaDesa;
    this.username = this.params.username;

    let getisReadOnly = this.params.isReadOnly;
    this.isReadOnly = (getisReadOnly != null) ? getisReadOnly : 0;

  }

  ngOnInit() {
  }


  async presentLoading() {
    this.loading = await this.loadingController.create({
      cssClass: 'my-custom-class',
      message: 'Loading...',
      mode: "ios",
      duration: 5000
    });
    this.loading.present();
  }

  async presentToast(color, header, message) {
    const toast = await this.toastController.create({
      header: header, //"Warning",
      message: message, //"Mohon isi semua formulir",
      color: color, //"danger",
      mode: "ios",
      position: "middle",
      duration: 3000
    });
    toast.present();
  }

  async presentAlertConfirm() {
    const alert = await this.alertController.create({
      cssClass: 'my-custom-class',
      header: 'Konfirmasi !',
      message: 'Pastikan data yang Anda masukan benar !',
      mode: 'ios',
      backdropDismiss: false,
      buttons: [
        {
          text: 'Tutup',
          role: 'cancel',
          cssClass: 'secondary',
          handler: (blah) => {
            // console.log('Confirm Cancel: blah');
          }
        }, {
          text: 'Kirim',
          handler: () => {
            this.submitRegistration();
          }
        }
      ]
    });

    await alert.present();
  }

  async presentAlertConfirmError() {
    const alert = await this.alertController.create({
      cssClass: 'my-custom-class',
      header: 'Username sudah digunakan',
      message: 'Anda akan di kembalikan ke halaman sebelumnya',
      mode: 'ios',
      backdropDismiss: false,
      buttons: [
        {
          text: 'Oke',
          handler: () => {
            this.goBack();
          }
        }
      ]
    });

    await alert.present();
  }

  confirmation() {

    if (this.username != '' &&
      this.fullname != '' &&
      this.phoneNumber != '' &&
      this.password != '' &&
      this.repassword != '') {

      if (this.password.length >= 6) {
        if (this.password == this.repassword) {
          this.presentAlertConfirm();
        } else {
          this.errorPassword = true;
          setTimeout(() => {
            this.errorPassword = false;
          }, 10000);
          this.presentToast('danger', 'Re-Password', 'Re-Password tidak cocok');
        }
      } else {
        this.presentToast('danger', 'Password', 'Password minimal 6 karakter');
      }

    } else {
      this.presentToast('danger', 'Warning', 'Mohon isi semua data');
    }

  }

  submitRegistration() {

    this.presentLoading();

    // this.params
    let params =
    {
      email: this.username,
      password: this.password,
      name: this.fullname,
      phone_number: this.phoneNumber,
      geo_id: this.params.geo_id,
      geo_auth_code: this.params.geo_auth_code,
      position: this.isKepalaDesa
    };
    this.subscribe = this.apiService.post('users', params).subscribe(
      async (data) => {

        this.loginToApps();

      },
      error => {
        this.presentAlertConfirmError();
      }
    );

  }

  loginToApps() {

    this.subsLogin = this.authService.loginUser(this.username, this.password)
      .pipe(first())
      .subscribe(
        data => {
          this.loading.dismiss();
          // console.log(data);          
          this.NavController.navigateRoot('/home', { replaceUrl: true });

        },
        error => {
          // this.presentAlertConfirm();
          // console.log(error);
        });

  }

  goBack() {
    this.NavController.navigateBack(['/front/form3',
      { isKepalaDesa: this.isKepalaDesa, isReadOnly: this.isReadOnly }]);
  }

}
